import { Component, OnInit } from '@angular/core';
import * as mapBox from "mapbox-gl";
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-section-contactos',
  templateUrl: './section-contactos.component.html',
  styleUrls: ['./section-contactos.component.scss']
})
export class SectionContactosComponent implements OnInit {

  constructor() { }
  map:any;
  ngOnInit() {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        let coord = {
          latitud: position.coords.latitude,
          longitud: position.coords.longitude,
        };
        console.log(coord);
        this.createMapBox(coord);
      });
    }
  }


  
  createMapBox(coordenadas) {
    mapBox.accessToken = environment.mapbox_key;
    this.map = new mapBox.Map({
      container: "map_box",
      style: "mapbox://styles/mapbox/streets-v11",
      center: [coordenadas.longitud, coordenadas.latitud], // starting position
      zoom: 9, // starting zoom
    });
    this.map.addControl(new mapBox.NavigationControl());
    this.map.addControl(
      new mapBox.GeolocateControl({
        positionOptions: { enableHighAccuracy: true },
        trackUserLocation: true,
      })
    );
  }

}
